// BASIC
import React, { Component } from 'react'
import styled from 'styled-components'
import firebase, {au, db} from '../../../Config/firebase'

const MainChatComponent = styled.div`
	padding: 20px;
`
const Friend = styled.div`
	display: flex;
	align-items: center;
	margin: 20px;
`
const Avatar = styled.div`
	width: 50px;
	height: 50px;
	background: url("${props => props.src}") no-repeat center center;
	background-size: cover;
	border-radius: 100%;
	margin-right: 10px;
`


class MainChat extends Component {
	state = {
		userId: '',
		friends: []
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				db.collection('users').doc(user.uid).update({
					'info.lastVisit': new Date(),
					'info.active': true
				})
				const friends = []
				this.setState({userId: user.uid})
				db.collection('users').doc(user.uid).get().then(snap => {
					Object.keys(snap.data().chats).forEach(friendId => {
						db.collection('users').doc(friendId).get().then(snap => {
							friends.push(snap.data().info)
							this.setState({
								friends
							})
						})
					})
				})
			}
		})
	}
	componentWillUnmount() {
		if (this.state.userId) {
			db.collection('users').doc(this.state.userId).update({
				'info.lastVisit': new Date(),
				'info.active': false
			})
		}
	}
	render() {
		return (
			<MainChatComponent>
				{this.state.friends.map(({uid, nick, avatar}) => {
					// console.log(friend);
					return <Friend key={uid}>
						<Avatar src={avatar} />
						{nick}
					</Friend>
				})}
			</MainChatComponent>
		);
	}
}

export default MainChat